body {
    background-color: #6E5C62;
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100vh;
    width: 100vw;
}

.container {
    width: 70vw;
    height: 6em;
    font-size: 63px;
}

.letter {
    background-color: #615458;
    height: 1em;
    width: 1em;
    border-radius: .15em;
    text-align: center;
    line-height: .8em;
}

.termo-row {
    width: 100%;
    height: 20%;
    display: flex;
    flex-shrink: 0;
    flex-direction: row;
    gap: .1em;
    justify-content: center;
 

}

.active {
    border: 1px solid black;
}